.game-container {
	width: 800px;
	margin: 0 auto;
	// padding: 20px;
	font-family: Arial, sans-serif;
}

.control-panel {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 20px;
	padding: 15px;
	background: #f5f5f5;
	border-radius: 10px;

	.level-control {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 180px;
		gap: 10px;

		button {
			width: 30px;
			height: 30px;
			border: none;
			background: #007bff;
			color: white;
			border-radius: 5px;
			cursor: pointer;

			// &:disabled {
			//   background: #ccc;
			//   cursor: not-allowed;
			// }
		}
	}

	.start-button {
		padding: 10px 20px;
		background: #28a745;
		color: white;
		border: none;
		border-radius: 5px;
		cursor: pointer;

		// &:disabled {
		// 	background: #ccc;
		// 	cursor: not-allowed;
		// }
	}

	.timer {
		font-size: 18px;
		font-weight: bold;
	}
}

.game-area {
	position: relative;
	// width: 800px;
	// height: 600px;
	margin: 0 auto;
	border: 1px solid grey;
	border-radius: 10px;
	overflow: hidden;
}

.game-canvas {
	display: block;
	// background: #ebf1f4;
	cursor: none;
}

.duck {
	position: absolute;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	transform: translate(-50%, -50%);
	transition: all 0.1s ease;
	border: 2px solid rgba(255, 255, 255, 0.8);

	&.duck--red {
		background: #ff4444;
	}
	&.duck--green {
		background: #44ff44;
	}
	&.duck--blue {
		background: #4444ff;
	}
}

.dog {
	position: absolute;
	width: 15px;
	height: 15px;
	border-radius: 50%;
	background: white;
	border: 2px solid #333;
	transform: translate(-50%, -50%);
	pointer-events: none;
	z-index: 100;
	box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
}

.start-screen,
.completion-screen {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: rgba(255, 255, 255, 0.95);
	padding: 30px;
	border-radius: 15px;
	text-align: center;
	z-index: 1000;

	button {
		margin-top: 15px;
		padding: 10px 20px;
		background: #007bff;
		color: white;
		border: none;
		border-radius: 5px;
		cursor: pointer;
	}
}
